import React from 'react';
import Types from 'prop-types';
import WithTextFormat from '../withTextFormat';

const TextareaWithFormat = props => (
  <WithTextFormat {...props} render={renderProps => <textarea {...renderProps} />} />
);

TextareaWithFormat.propTypes = {
  textFormatPattern: Types.string.isRequired,
  onChange: Types.func.isRequired,
};

export default TextareaWithFormat;



