import React from 'react';

const generateCodeBlock = (componentName, knobs, componentThis, extraPropsDocs = []) => {
  let arrayDocs = [];
  Object.values(knobs).map(knob => arrayDocs.push(...knob));

  arrayDocs = arrayDocs.map(knob => generateDocsWithUpdatedStateValue(knob, componentThis));

  if (extraPropsDocs.length) {
    arrayDocs.push(...extraPropsDocs);
  }

  return (
    <pre className="tw-docs-code ">
      {`<${componentName}
  ${arrayDocs.join('\n\r  ')}
/>`}
    </pre>
  );
};

const generateDocsWithUpdatedStateValue = (knob, componentThis) => {
  let value = '';
  if (componentThis.state[knob.state] || componentThis.state[knob.state] === false) {
    if (componentThis.state[knob.state].value) {
      ({ value } = componentThis.state[knob.state]);
    } else {
      value = componentThis.state[knob.state].toString();
    }
  }

  return `${knob.state} = {"${value || ''}"}`;
};

export default generateCodeBlock;



